@import "../../utils/Variable.scss";

.dashboardMainContainer {
    width: 100%;
    .dashboardNavBar {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        .titleName {
            font-size: 20px;
            font-weight: 500;
        }
        .btnGroup {
            display: flex;
            flex-direction: row;
            align-items: center;
            gap: 10px;
            .btnOutItem {
                border: 1px solid grey !important;
                padding: 4px 0px !important;
                min-width: 34px !important;
            }
            .btnFillItem {
                padding: 5px 6px !important;
                background-color: #333 !important;
                color: #fff !important;
                text-transform: none !important;
            }
        }
    }
    .graphContainer {
        padding-top: 16px;
        display: flex;
        flex-direction: row;
        gap: 12px;
        .graphItemBox {
            border: 1px solid #fff;
            border-radius: 6px;
            background-color: #fff;
        }
        .graphItemBoxb {
            border: 1px solid #fff;
            border-radius: 6px;
            background-color: #fff;
        }
        .graphItemBoxc {
            border-radius: 6px;
            background-image: url('../../assets/image/cardBgImage.jpg');
            width: 260px;
            background-repeat: no-repeat;
            background-size: cover; // Ensures the background image covers the entire container
            background-position: center;
            padding: 0px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .topBtnContainer {
                width: 90%;
                margin: 10px 10px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                .popularBtnItem {
                    background-color: #fff !important;
                    color: #121212 !important;
                    text-transform: none !important;
                    cursor: pointer;
                }
                .playBtnItem {
                    cursor: pointer;
                    color: #fff !important;
                    background-color: #121212 !important;
                    padding: 4px 8px !important;
                    min-width: 0px !important;
                }
            }
            .graphItemBoxcFooter {
                margin: 10px;
                font-size: 22px;
                font-weight: 500;
                color: #fff;
            }
        }
    }
}