@import "../../utils/Variable.scss";

.sideBarItem {
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.appNameContainer {
    // border: 1px solid $border-color;
    border-radius: 8px;
    background-color: $white-color;
    padding: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
}

.sideFlexContainer {
    height: 67.6vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.appLogo {
    font-size: 20px;
    padding: 8px 12px;
    background-color: $white-color-shade; 
}

.companyName {
    font-size: 18px;
}

.companySlogan {
    font-size: 13px;
    color: #333;
}

.userContainer {
    border-radius: 8px;
    background-color: $primary-background-color;
    padding: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
}

.userImage {
    height: 36px;
    // font-size: 20px;
    // padding: 8px 12px;
    background-color: $secondary-background-color;  
}

.userName {
    font-size: 18px;   
}

.signOutBtn {
    font-size: 13px;
    color: #333;
}

.btnItemContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 8px;
    padding: 10px;
    border-radius: 4px;
    cursor: pointer;
    text-decoration: none;
    color: $font-primary-color;
}

.btnOuterContainer {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.btnItemContainer:hover {
    background-color: $secondary-background-color;
}